'use client';

import { useBlockDetails } from '@/lib/useEtherscanData';
import { useParams } from 'next/navigation';
import { Loader2 } from 'lucide-react';

export default function BlockDetailPage() {
  const params = useParams();
  const blockNumber = parseInt(params.blockNumber as string);
  const { data: block, isLoading } = useBlockDetails(blockNumber);

  if (isLoading) {
    return (
      <div className="flex justify-center items-center h-64">
        <Loader2 className="w-8 h-8 animate-spin text-blue-600" />
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold">Block #{block?.number}</h1>
      {/* Block details UI */}
    </div>
  );
}
